.List_wrap {
    position: fixed;
    top: 8rem;
    right: 0;
    width: 100%;
    max-width: 400px;
    z-index: 100;
    background: #eaf1ff;
    height: calc(100vh - 8rem);
    padding: 3rem;
    box-sizing: border-box;
    overflow-y: auto;
}

.ListInput {
    display: block;
    width: 100%;
    line-height: 4rem;
    height: 4rem;
    padding: 0 1rem;
    font-size: 1.6rem;
    margin-bottom: 1rem;
}

.btn_addList {
    width: 100%;
    line-height: 6rem;
    height: 6rem;
    font-size: 2.4rem;
    text-align: center;
    color: #fff;
    background: #588ad6;
    border-radius: 1rem;
    margin-bottom: 1rem;
}

.mapList_wrap>ul {margin-bottom: 4rem;}

.mapListSec {
    display: flex;
    align-items: start;
    flex-wrap: wrap;
}

.mapListSec>button {
    width: 3rem;
    height: 4rem;
    font-size: 2rem;
    margin: 1rem 0;
    background: none;
    color: #1635638c;
    flex-shrink: 0;
}

.mapListDiv {
    padding: 0 1rem;
    width: calc(100% - 6rem);
}

.mapListDivLock {width: calc(100% - 13rem);}

.mapListDiv>div {overflow: hidden;}

.mapListDiv input {margin: 0;}

.mapListObject {
    background: #c6e0ff;
    padding: 1rem;
    width: calc(100% - 8rem);
    flex-shrink: 0;
    margin: 0 4rem 2rem;
}

.mapListName {padding: 1rem 0;}
.mapListName input {width: calc(100% - 3rem); float: left;}
.mapListName button {
    width: 2rem;
    height: 4rem;
    line-height: 4rem;
    font-size: 2.4rem;
    float: right;
    background: none;
    margin-left: 1rem;
}

.mapListObject li {
    overflow: hidden;
    line-height: 4rem;
    font-size: 2rem;
    margin-bottom: 1rem;
}

.mapListObject input {
    float: right;
    width: calc(100% - 3rem);
}

.btn_addMap {
    background: #577dc4;
    width: 4rem;
    height: 4rem;
    text-align: center;
    font-size: 2.4rem;
    line-height: 4rem;
    color: #fff;
    margin: 0 auto;
    display: block;
    border-radius: 50%;
}

.mapLockOn {color: #f80 !important;}

.fixOrRandom {
    margin-bottom: 2rem;
    text-align: center;
}

.fixOrRandom>button {
    display: inline-block;
    width: 6rem;
    height: 4rem;
    border-radius: 2rem;
    padding: 3px;
    box-sizing: border-box;
}

.fixOrRandom>button>span {
    display: inline-block;
    width: 3.2rem;
    height: 3.2rem;
    border-radius: 50%;
    background: #fff;
}

.fixOrRandom>span {
    display: inline-block;
    margin-left: 1rem;
    line-height: 4rem;
    vertical-align: bottom;
}

.mapFixOrder {
    width: 6rem;
    height: 4rem;
    margin: 1rem 0 1rem 1rem;
    padding: 1rem;
    box-sizing: border-box;
    font-size: 1.6rem;
}

.toggleFix {
    border: 1px solid #aaa;
    background: #00c43b;
    text-align: right;
}

.toggleRandom {
    border: 1px solid #aaa;
    background: #ccc;
    text-align: left;
}

.fixOk {background: #ffe000;}